import { FormControlLabel, Radio, RadioGroup, Slider } from '@mui/material';
import React, { useCallback, useEffect, useState } from 'react';

const products = {
	monthly: {
		landed1: {
			fee: 49.99,
			interest: 0.283282724670457,
			factor: 0.09666667,
		},
		landed2: {
			fee: 53.99,
			interest: 0.408837640517603,
			factor: 0.061666,
		},
		settled: {
			fee: 55.99,
			interest: 0.229922176380833,
			factor: 0.028186,
		},
	},
	biweekly: {
		landed1: {
			fee: 21.99,
			interest: 0.283282724670457,
			factor: 0.04437391,
		},
		landed2: {
			fee: 22.99,
			interest: 0.408837640517603,
			factor: 0.02829548,
		},
		settled: {
			fee: 26.99,
			interest: 0.229922176380833,
			factor: 0.01297345,
		},
	},
};

const getDateDue = (initialDate, frequency, instalments) => {
	return '15/02/2022';
};

// const pchFormatter = new Intl.NumberFormat('en-US', {
// 	style: 'currency',
// 	currency: 'USD',
// 	maximumFractionDigits: 2,
// });

function Calculator() {
	const [instalment, setInstalment] = useState(0);
	const [frequency, setFrequency] = useState('biweekly');
	const [duration, setDuration] = useState('settled');
	const [amount, setAmount] = useState(5000);
	const [disableOptions, setDisabledOptions] = useState({ landed1: false, landed2: false, settled: false });

	const handleAmount = e => {
		setAmount(e.target.value);
	};

	const refreshTable = useCallback((amount, frequency, duration, instalment, info) => {
		let years = 5;
		if (duration === 'landed1') years = 1;
		if (duration === 'landed2') years = 2;

		const multiplier = frequency === 'biweekly' ? 26 : 12;

		const instalments = years * multiplier;
		const initialDate = new Date();
		const table = [];

		let balance = amount;

		for (let i = 0; i < instalments; i++) {
			const dateDue = getDateDue(initialDate, frequency, instalments);
			const principal = balance;
			const interest = principal * (info.interest / multiplier);
			const amortization = instalment - info.fee - interest;
			balance = principal - amortization;
			const row = {
				paymentNumber: i + 1,
				dateDue,
				principal,
				interestRate: info.interest / multiplier,
				interest,
				amortization,
				fee: info.fee,
				payment: instalment,
				balance,
			};
			table.push(row);
		}

		// setAmortizationTable(table);
	}, []);

	useEffect(() => {
		if (amount > 0) {
			const info = products[frequency][duration];

			if (amount < 2000 && duration !== 'landed1') {
				setDuration('landed1');

				setDisabledOptions({
					landed1: false,
					landed2: true,
					settled: true,
				});
			} else if (amount < 3000 && amount >= 2000) {
				if (duration === 'settled') setDuration('landed2');

				setDisabledOptions({
					landed1: false,
					landed2: false,
					settled: true,
				});
			} else if (amount < 4000 && amount >= 3000) {
				setDuration('landed2');

				setDisabledOptions({
					landed1: true,
					landed2: false,
					settled: true,
				});
			} else if (amount < 5000 && amount >= 4000) {
				if (duration === 'landed1') setDuration('settled');

				setDisabledOptions({
					landed1: true,
					landed2: false,
					settled: false,
				});
			} else if (amount >= 5000) {
				setDuration('settled');

				setDisabledOptions({
					landed1: true,
					landed2: true,
					settled: false,
				});
			}

			const tempInstalment = amount * info.factor + info.fee;
			setInstalment(tempInstalment);

			// refreshTable(amount, frequency, duration, tempInstalment, info);
		}
	}, [amount, frequency, duration, refreshTable]);

	const handleFrequencySelect = useCallback(e => {
		setFrequency(e.target.value);
	}, []);

	const handleDurationSelect = useCallback(e => {
		setDuration(e.target.value);
	}, []);

	const sliderFormat = (value, index) => {
		return `$ ${value}`;
	};

	const handleApply = () => {
		var win = window.open('/apply-now', '_blank');
		win.focus();
	};

	return (
		<div className='p-4'>
			<div className='grid grid-cols-1 md:grid-cols-3 gap-4 shadow-xl rounded-t-3xl rounded-b-3xl md:rounded-r-3xl'>
				<div className='md:flex h-full'>
					{/* Aqui tengo los dos divs, que en movil esta uno debajo del otro y en md uno al lado del otro */}
					<div className='bg-gradient-to-b md:bg-gradient-to-r from-[#90FFBE] to-[#86CDFF]  flex items-center justify-center flex-col flex-auto pt-8 md:pt-0 rounded-t-3xl md:rounded-l-3xl md:rounded-tl-3xl md:rounded-t-none'>
						<div className='text-[#112446]'>
							{frequency === 'biweekly' ? 'Bi-Weekly' : 'Monthly'} instalments of:
						</div>{' '}
						<div className='text-[#112446] w-[220px] text-5xl font-extrabold mt-2'>$ {instalment.toFixed(2)}</div>
						<div
							className='w-[50%] bg-black text-white rounded-full text-sm text-center p-2 shadow-xl mt-8 cursor-pointer'
							onClick={handleApply}
						>
							APPLY NOW!
						</div>
					</div>
					{/* en este cambio el estilo para que sea arriba o abajo el border */}
					<div className='bg-[#86CDFF] w-20 h-10 md:h-full down-arrow md:right-arrow flex-1'></div>
				</div>
				<div className='md:col-span-2 p-6 bg-white rounded-b-3xl md:rounded-r-3xl'>
					<div className='text-2xl my-4'>
						You will receive: <span className='font-extrabold text-gray-800'>$ </span>
						<span className='text-4xl font-bold'>
							<input
								type='text'
								value={amount}
								onChange={handleAmount}
								variant='standard'
								className='w-32 font-extrabold text-gray-800'
								min='1500'
								max='10000'
							/>
						</span>
					</div>
					<div className='flex my-4' style={{ width: '90%' }}>
						<div className='flex-auto'>Min:$ 1.500</div>
						<div>Max:$ 10.000</div>
					</div>
					<div>
						<Slider
							valueLabelDisplay='auto'
							valueLabelFormat={sliderFormat}
							aria-label='pretto slider'
							value={amount}
							min={1500}
							max={10000}
							style={{ width: '90%' }}
							onChange={e => setAmount(e.target.value)}
						/>
					</div>
					<div className='flex items-center gap-2'>
						<span>Paid:</span>
						<RadioGroup
							row
							aria-label='position'
							name='position'
							value={frequency}
							onChange={handleFrequencySelect}
						>
							<FormControlLabel
								value='biweekly'
								control={<Radio color='primary' />}
								label='Bi-Weekly'
								labelPlacement='end'
							/>
							<FormControlLabel
								value='monthly'
								control={<Radio color='primary' />}
								label='Monthly'
								labelPlacement='end'
							/>
						</RadioGroup>
					</div>
					<div>
						<span>For:</span>
						<RadioGroup
							row
							aria-label='position'
							name='position'
							defaultValue='settled'
							value={duration}
							onChange={handleDurationSelect}
						>
							<FormControlLabel
								value='landed1'
								control={<Radio color='primary' />}
								label='1 Year'
								labelPlacement='end'
								disabled={disableOptions.landed1}
							/>
							<FormControlLabel
								value='landed2'
								control={<Radio color='primary' />}
								label='2 Years'
								labelPlacement='end'
								disabled={disableOptions.landed2}
							/>
							<FormControlLabel
								value='settled'
								control={<Radio color='primary' />}
								label='5 Years'
								labelPlacement='end'
								disabled={disableOptions.settled}
							/>
						</RadioGroup>
					</div>
					<div className='text-xs my-2'>*Each case must be analyzed to determine the final amounts. </div>
				</div>
			</div>

			{/* <div className='md:col-span-3'>
				{view?.version === 'short' ? (
					<div className='border-[#606060] border-2 border-dashed border-t-0 w-[90%] mx-auto p-2 text-center rounded-b-2xl'>
						<span className='text-xs uppercase underline text-[#606060] cursor-pointer' onClick={handleApply}>
							See the full payment schedule
						</span>
					</div>
				) : (
					<div className='border-[#606060] border-2 border-dashed border-t-0 w-[90%] mx-auto p-2 text-center rounded-b-2xl'>
						<table className='w-full text-sm'>
							<thead>
								<tr>
									<td>#</td>
									<td>Principal</td>
									<td>I. Rate</td>
									<td>Interest*</td>
									<td>Amortization</td>
									<td>Fee</td>
									<td>Payment</td>
									<td>Balance *</td>
								</tr>
							</thead>
							<tbody>
								{amortizationTable.map(row => (
									<tr key={row.paymentNumber}>
										<td>{row.paymentNumber}</td>
										<td>{pchFormatter.format(row.principal)}</td>
										<td>{(row.interestRate * 100).toFixed(2)}%</td>
										<td>{pchFormatter.format(row.interest)}</td>
										<td>{pchFormatter.format(row.amortization)}</td>
										<td>{pchFormatter.format(row.fee)}</td>
										<td>{pchFormatter.format(row.payment)}</td>
										<td>{row.balance < 0 ? '0.00' : pchFormatter.format(row.balance)}</td>
									</tr>
								))}
							</tbody>
						</table>
					</div>
				)}
			</div> */}
		</div>
	);
}

export default Calculator;
